/*
//
// Link styles
// =============================================================================
*/

a {
  color: $link-color;
  text-decoration: none;
  /*@include transition(.3s color, .3s background);*/

  &:hover,
  &:active,
  &:focus {
    color: $link-color-hover;
    text-decoration: underline;

    &.icon {
      text-decoration: none;
    }
  }
}

/*
// Links within paragraphs
*/
p a {

}

/*
// current / active link
*/
.current,
.active {
  & > a {
    cursor: text;
    text-decoration: underline;
  }
}
/*
// links in headlines
*/
h1,
h2,
h3,
h4,
h5,
h6,
.alpha,
.beta,
.gamma,
.delta,
.epsilon,
.zeta {
  a {
    color: inherit;
    &:hover,
    &:active,
    &:focus {
      color: $link-color-hover;
      text-decoration: none;
    }
  }
}
